"'use client';"
import React from 'react';

export default function PrivacyPage() {
  return (
    <div style={{
      minHeight: '100vh',
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
      background: '#fafafa'
    }}>
      <div style={{
        width: 370,
        background: '#fff',
        borderRadius: 12,
        boxShadow: '0 2px 16px rgba(0,0,0,0.07)',
        padding: 24,
        position: 'relative'
      }}>

        {/* Header */}
        <div style={{
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'space-between',
          marginBottom: 24
        }}>
          <span style={{ fontWeight: 600, fontSize: 18 }}>Data and Privacy</span>
          <button
            onClick={() => window.history.back()}
            style={{
              fontSize: 22,
              fontWeight: 600,
              background: 'none',
              border: 'none',
              cursor: 'pointer',
              padding: 0,
              lineHeight: 1
            }}
            aria-label="Close"
          >
            ×
          </button>
        </div>

        {/* Options */}
        <div style={{
          borderTop: '1px solid #eee',
          borderBottom: '1px solid #eee'
        }}>
          {/* Deactivate */}
          <div style={{ padding: '16px 0', borderBottom: '1px solid #eee', cursor: 'pointer' }}>
            <div style={{ fontWeight: 500 }}>Deactivate My Account</div>
            <div style={{ color: '#888', fontSize: 13 }}>
              Temporarily deactivate your profile without losing your data.<br />
              You can reactivate at any time.
            </div>
          </div>

          {/* Delete */}
          <div style={{ padding: '16px 0', borderBottom: '1px solid #eee', cursor: 'pointer' }}>
            <div style={{ fontWeight: 500 }}>Delete My Account</div>
            <div style={{ color: '#888', fontSize: 13 }}>
              Permanently delete all your data.
            </div>
          </div>

          {/* Privacy Policy */}
          <div
            onClick={() => window.open("/privacy-policy", "_blank")}
            style={{ padding: '16px 0', display: 'flex', alignItems: 'center', cursor: 'pointer' }}
          >
            <div style={{ flex: 1 }}>
              <div style={{ fontWeight: 500, textTransform: 'capitalize' }}>Privacy Policy</div>
              <div style={{ color: '#888', fontSize: 13 }}>
                Read more about our privacy policy.
              </div>
            </div>
            <span style={{ fontSize: 18, marginLeft: 8 }}>↗</span>
          </div>
        </div>

        {/* Save Button */}
        <button style={{
          marginTop: 28,
          width: '100%',
          background: '#22c55e',
          color: '#fff',
          border: 'none',
          borderRadius: 6,
          padding: '12px 0',
          fontWeight: 600,
          fontSize: 16,
          cursor: 'pointer'
        }}>
          Save
        </button>
      </div>
    </div>
  );
}
